<template>
  <div class="main">
    <!-- {{this.showMETform}} -->
    <hr>
    <b-row>
      <b-col cols="1" class="my-1"> Division:</b-col>
      <b-col cols="2" class="my-1">
        <b-form-select
          v-model="division"
          :options="divisionData"
          required
        ></b-form-select>
      </b-col>
      <b-col cols="1"> Department:</b-col>
      <b-col cols="2">
        <b-form-select
          v-model="department"
          :options="departmentData"
          required
        ></b-form-select>
      </b-col>
      <b-col cols="2">
        <b-button @click="queryTransactionList()">Show</b-button>
      </b-col>
    </b-row>
    <hr>
    <div id="empList" v-show="empList">
      <b-row>
        <b-col md="8" class="my-1">
          <b-form-group label-cols-sm="3" label="Filter" class="mb-0">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Type to Search"></b-form-input>
              <b-input-group-append>
                <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>
        <b-col md="3" class="my-1">
          <b-form-group label-cols-sm="3" label="Per page" class="mb-0">
            <b-form-select v-model="perPage" :options="pageOptions"></b-form-select>
          </b-form-group>
        </b-col>
      </b-row>
      <b-table
        :items="salaryTransactions"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter="filter"
      >
      <colgroup><col><col></colgroup>
        <template v-slot:row-details="row">
          <b-card>
            <ul>
              <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
            </ul>
          </b-card>
        </template>
        <template v-slot:cell(action)="row">
          <b-button  variant="outline-secondary"
            size="sm"
            @click="details(row.item)"
          >{{ row.detailsShowing ? 'Hide' : 'Show' }} </b-button>
          <!-- <b-button v-show="row.item.empCode===100"
            size="sm"
            @click="details(row.item)"
          >{{ row.detailsShowing ? 'Hide'  : 'Show' }} </b-button> -->
        </template>
      </b-table>
      <hr />
      <b-row>
        <b-col md="6" class="my-1">
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            class="my-0"
          ></b-pagination>
        </b-col>
      </b-row>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import moment from 'moment'
export default {
  props: {
    setActiveTab: { type: Function }
  },
  data () {
    return {
      showMET: false,
      empList: true,
      selectMode: 'single',
      division: {},
      department: {},
      selected: [],
      divisionData: [{ text: 'All', value: 0 }, { text: 'Press', value: 101 }, { text: 'Publishing', value: 102 }],
      departmentData: [{ text: 'All', value: 0 }],
      salaryTransactions: [],
      totalRows: 1,
      fields: [
        { key: 'selected', label: '' },
        {
          key: 'id',
          label: 'Employee Code',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left'
        },
        {
          key: 'empName',
          label: 'Employee Name',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left'
        },
        {
          key: 'basicSalary',
          label: 'Basic Salary',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },

        {
          key: 'rentAllowance',
          label: 'Rent Allowance',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'travelAllowance',
          label: 'Travel Allowance',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'spageAllowance',
          label: 'Special Allowance',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'nShiftAllowance',
          label: 'Night shift allowance',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'otherAllowanceTotal',
          label: 'Other Allowance',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'otTotal',
          label: 'OT total',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'subTotal',
          label: 'Sub total',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },

        {
          key: 'loanDeduction',
          label: 'Loan Deduction',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'hBankLoan',
          label: 'Housing Bank Loan',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'gosiAmt',
          label: 'Gosi Amount',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'lateHrs',
          label: 'Late Hours',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'otherDeductionTotal',
          label: 'Other Deduction',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },
        {
          key: 'netSalary',
          label: 'Net Salary',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right'
        },

        { key: 'action', label: 'Action' }
      ],
      currentPage: 1,
      perPage: 5,
      pageOptions: [2, 5, 10, 15],
      // sortBy: null,
      // sortDesc: false,
      // sortDirection: "asc",
      filter: null,
      modalInfo: { title: '', content: '' }
    }
  },
  mounted () {
    // this.fetchEmployeeList1({
    //   sorting: { fieldName: 'id', Sort: 'DESC' },
    //   filter: [{
    //     fieldName: 'id',
    //     value: ''
    //   }
    //   ]
    // })
    this.fetchSalaryTransactions()
  },
  computed: {
    ...mapGetters(['employee', 'showMETform', 'employeeList'])
  },
  methods: {
    ...mapActions(['METformDisplay', 'fetchemployeeMET']),
    details (item) {
      console.log('call setDisplay', item.id)
      this.fetchemployeeMET(item.id)
      this.$emit('setActiveTab')
    },
    fetchSalaryTransactions () {
      const url =
        process.env.VUE_APP_API_URL + '/salaryList'
      fetch(url)
        .then(res => res.json())
        .then(data => {
          this.salaryTransactions = data
          this.totalRows = this.salaryTransactions.length
        })
        .catch(err => console.error(err))
    },
    queryTransactionList () {
      const url =
        process.env.VUE_APP_API_URL + `/salaryList/${this.division}/${this.department}`
      fetch(url)
        .then(res => res.json())
        .then(data => {
          this.salaryTransactions = data
          this.totalRows = this.salaryTransactions.length
        })
        .catch(err => console.error(err))
    },
    fetchDepartmentList () {
      const url =
        process.env.VUE_APP_API_URL + '/department'
      fetch(url)
        .then(res => res.json())
        .then(data => {
          this.departmentData = data
          this.totalRows = this.departmentData.length
        })
        .catch(err => console.error(err))
    },
    fetchDepartmentListForDivision (divisionCode) {
      let x = []
      this.departmentData = [{ text: 'All', value: 0 }]
      const url =
        process.env.VUE_APP_API_URL + `/department/${divisionCode}`
      fetch(url)
        .then(res => res.json())
        .then(data => {
          x = data
          x.forEach(d => this.departmentData.push({ text: d.departmentName, value: d.id }))

          this.totalRows = x.length
          // Object.entries(x).forEach(([key, value]) => {
          //   y.text = key
          //   y.value = value
          //   this.departmentData.push(y)
          // })
        })
        .catch(err => console.error(err))
    },
    formatData (value, x) {
      if (!value) return ''
      if (x.includes('date')) {
        // console.log('col name includes date')
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else if (x.includes('Date')) {
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else {
        // console.log('not formated')
        return value
      }
    }
  },
  watch: {
    division: {
      handler: function () {
        this.fetchDepartmentListForDivision(this.division)
      }
    }
  }
}
</script>
<style scoped>
.main {
  width: 95%;
  margin-left: 40px;
  margin-right: 40px;
  /* font-family: "Arial", Times, serif; */
  font-size: small;
}
.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}
</style>
